.help {
    padding-top: 112.5px;
    padding-bottom: 265px;
    background-color: #1B171E;

    @media(max-width:767px) {
        padding-top: 20px;
        padding-bottom: 55px;
    }

    position: relative;
    z-index: 3;

    &::before {
        content: '';
        height: 654px;

        background: linear-gradient(180deg, #211C25 0%, rgba(36, 30, 40, 0) 100%);

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        z-index: 2;

        @media(max-width:1023px) {
            display: none;
        }
    }


    &::after {
        content: '';
        background-image: url("../img/noise.webp");
        background-size: cover;
        background-repeat: no-repeat;
        opacity: .1;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
    }

    .katana {
        width: 618.56px;
        height: 779px;

        position: absolute;
        bottom: 148px;
        right: 238px;
        z-index: 4;

        transform: translateY(-60px);
        transition: 1s linear;
        transition-delay: 1s;

        @media(max-width:1919px) {
            width: 438.56px;
            height: 569px;
            position: absolute;
            bottom: 148px;
            right: 8px;
        }

        @media(max-width:1439px) {
            width: 279.56px;
            height: 319px;
        }

        @media(max-width:767px) {
            width: 252.28px;
            height: 317.72px;
            bottom: unset;
            top: 40px;
            right: -102px;

            &::after {
                content: '';
                width: 174.77px;
                height: 68.57px;

                background-image: url("../img/stone2.svg");
                background-size: contain;

                position: absolute;
                right: 36px;
                bottom: -13px;
                z-index: -1;
            }
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }

        &::before {
            content: '';

            width: 10px;
            height: 500px;

            background: #FFFFFF;
            filter: blur(40px);
            opacity: .9;

            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translateX(-50%);
            z-index: -1;

            @media(max-width:1439px) {
                height: 280px;
            }

            @media(max-width:767px) {
                display: none;
            }
        }

        &.anim-active {
            transform: none;
        }
    }

    .stone {
        width: 160.2px;
        height: 94.77px;

        position: absolute;
        right: 422px;
        bottom: 121px;
        z-index: 4;

        @media(max-width:1919px) {
            width: 140.2px;
            height: 74.77px;
            position: absolute;
            right: 162px;
            bottom: 121px;
        }

        @media(max-width:1439px) {
            width: 100.2px;
            height: 54.77px;
            position: absolute;
            right: 102px;
        }

        @media(max-width:767px) {
            width: 60.2px;
            height: 34.77px;
            right: -9px;
            top: 340px;
            bottom: unset;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }

    .stars {
        width: 612.62px;
        height: 306.7px;

        position: absolute;
        right: 141px;
        top: 299px;
        z-index: 3;

        @media(max-width:1919px) {
            width: 442.62px;
            height: 175.7px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }
}

.help__inner {
    position: relative;
    z-index: 4;
}

.help__title {
    text-align: center;
    margin-bottom: 65px;

    @media(max-width:1919px) {
        margin-bottom: 35px;
    }

    @media(max-width:767px) {
        max-width: 263px;
        margin: 0 auto 20px;
    }
}

.help__descr {
    max-width: 699px;
    margin-bottom: 17px;

    font-weight: normal;
    font-size: 16px;
    line-height: 149.4%;
    text-align: center;
    color: rgba(255, 255, 255, 0.69);

    @media(max-width:1919px) {
        font-size: 14px;
    }

    @media(max-width:767px) {
        width: 69%;
        min-width: 182px;
        text-align: start;
    }
}

.help__list {
    display: flex;
    margin-bottom: 33px;

    @media(max-width:1023px) {
        justify-content: center;
    }

    @media(max-width:767px) {
        flex-direction: column;
        align-items: center;

    }
}

.help__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 169px;

    &:first-child {
        max-width: 185px;
    }

    &+& {
        margin-left: 46px;

        @media(max-width:767px) {
            margin-left: 0;
            margin-top: 20px;
        }
    }
}

.help__icon {
    width: 114.97px;
    height: 114.97px;

    @media(max-width:1439px) {
        width: 80px;
        height: 81px;

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }
}

.help__text {
    font-weight: bold;
    font-size: 18px;
    line-height: 126.4%;
    text-align: center;
    color: #FFFFFF;
}

.help__info {
    max-width: 710px;
    padding: 23px 65px 21px;

    background: #121013;
    border-radius: 25px;

    font-weight: normal;
    font-size: 16px;
    line-height: 149.4%;
    text-align: center;
    color: rgba(255, 255, 255, 0.69);

    @media(max-width:1023px) {
        font-size: 14px;
        max-width: 540px;
    }

    @media(max-width:767px) {
        padding: 23px 17px;
    }
}

.help__bottom {
    height: 300.62px;

    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;

    @media(max-width:1919px) {
        height: 270.62px;
    }

    @media(max-width:1439px) {
        height: 230.62px;
    }

    @media(max-width:767px) {
        display: none;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: top;
    }
}