.info {
    background: #1B171E;

    position: relative;
    z-index: 2;

    &::before {
        content: '';
        background-image: url("../img/noise.webp");
        background-size: cover;
        background-repeat: no-repeat;
        opacity: .1;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
}


.info__inner {
    position: relative;
    z-index: 3;
}

.info__title {
    margin: 0 auto 62px;
    width: fit-content;
    text-align: center;

    position: relative;

    @media(max-width:1439px) {
        margin-bottom: 0;
    }

    @media(max-width:1023px) {
        padding-top: 18px;
        margin-bottom: 60px;
    }

    &::before {
        content: '';
        width: 298px;
        height: 38.5px;

        background-image: url("../img/info-elem.svg");
        background-size: contain;
        background-repeat: no-repeat;

        position: absolute;
        left: -49px;
        bottom: -2.5px;

        @media(max-width:1023px) {
            width: 170px;
            height: 22px;
            left: 50%;
            transform: translateX(-50%);
            bottom: -18.5px;
        }
    }
}


.info__content {
    display: flex;
    align-items: flex-end;

    @media(max-width:1023px) {
        align-items: center;
        flex-direction: column;
    }
}

.info__logo {
    width: 654.49px;
    height: 492px;
    margin-right: 50px;

    @media(max-width:1023px) {
        margin-right: 0;
        margin-bottom: 60px;
    }

    @media(max-width:767px) {
        width: 183.58px;
        height: 138px;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain;
    }

    position: relative;

}

.info__descr {
    margin-bottom: 16px;
}

.info__descr-title {
    width: 473px;
    margin-bottom: 23px;

    font-weight: bold;
    font-size: 24px;
    line-height: 126.4%;
    color: #FFFFFF;

    @media(max-width:1919px) {
        font-size: 20px;
    }

    @media(max-width:1023px) {
        font-size: 18px;
        line-height: 126.4%;
        margin-bottom: 27px;
    }

    @media(max-width:767px) {
        max-width: 279px;
    }
}

.info__item {
    max-width: 462px;
    padding-left: 43px;

    font-weight: normal;
    font-size: 18px;
    line-height: 149.4%;
    color: rgba(255, 255, 255, 0.9);

    position: relative;

    @media(max-width:1919px) {
        font-size: 16px;
    }

    @media(max-width:1023px) {
        font-size: 14px;
        line-height: 149.4%;
    }

    &::before {
        content: '';
        width: 23px;
        height: 3px;

        background: #9C38DA;
        border-radius: 3px;

        position: absolute;
        top: 12px;
        left: 0;
    }

    &+& {
        margin-top: 18px;

        @media(max-width:1023px) {
            margin-top: 9px;
        }
    }
}