.intro {
    margin-bottom: 97px;

    background: linear-gradient(180deg, rgba(45, 28, 53, 0) 0%, #261930 100%);

    position: relative;
    z-index: 1;

    @media(max-width:1023px) {
        margin-bottom: 0;
    }

    &::after {
        content: '';
        background-image: url("../img/waves-bg.webp");
        background-size: cover;
        background-repeat: no-repeat;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -2;

        @media(max-width:1023px) {
            display: none;
        }
    }


    .blot {
        width: 1883px;
        height: 763px;

        background: rgba(133, 54, 170, 0.65);
        filter: blur(584px);

        position: absolute;
        left: -1188px;
        bottom: calc(100% + 57px);
    }

    .cloud1 {
        width: 330px;
        height: 180px;

        position: absolute;
        left: 108px;
        top: 217px;
        z-index: -1;
        transition: transform 1.6s linear;

        @media(max-width:1919px) {
            width: 190px;
            height: 100px;
            left: 88px;
            top: 177px;
        }

        @media(max-width:1439px) {
            width: 140px;
            height: 50px;
        }

        @media(max-width:1023px) {
            display: none;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    .cloud2 {
        width: 330px;
        height: 180px;

        position: absolute;
        right: 92px;
        top: 153px;
        z-index: 1;
        transition: transform 1.6s linear;

        @media(max-width:1919px) {
            width: 230px;
            height: 110px;
        }

        @media(max-width:1439px) {
            width: 170px;
            height: 90px;
        }

        @media(max-width:1023px) {
            display: none;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    .rock1 {
        width: 1508.87px;
        height: 963.79px;
        position: absolute;
        bottom: -155px;
        left: 0;


        @media(max-width:1919px) {
            height: 870px;
        }

        @media(max-width:1439px) {
            height: 850px;
        }

        @media(max-width:1023px) {
            width: 418.87px;
            height: 402px;
            bottom: -15px;
            left: -46px;

            img {
                display: block;
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }
    }

    .rock2 {
        height: 963.79px;
        width: 1508.87px;
        position: absolute;
        bottom: -227px;
        right: -269px;

        @media(max-width:1919px) {
            height: 893px;
            width: 1438px;
        }

        @media(max-width:1439px) {
            right: -429px;
        }

        @media(max-width:1023px) {
            display: none;
        }
    }

    .dots {
        width: 966.62px;
        height: 575.45px;

        position: absolute;
        left: 71px;
        top: 0px;

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }

        @media(max-width:1919px) {
            width: 476px;
            height: 325px;
        }

        @media(max-width:1439px) {
            width: 276px;
            height: 285px;
        }

        @media(max-width:1023px) {
            display: none;
        }
    }

    .dots2 {
        width: 167px;
        height: 172px;

        position: absolute;
        left: 0;
        top: 650px;

        transform: rotate(-33.58deg);

        @media(max-width:1919px) {
            width: 127px;
            height: 127px;
            top: 450px;
        }

        @media(max-width:1439px) {
            width: 87px;
            height: 87px;
            top: 320px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @media(max-width:1023px) {
            display: none;
        }
    }

    .moon {
        width: 701px;
        height: 660.15px;

        position: absolute;
        right: -280px;
        top: 235px;
        z-index: 3;
        transform: rotate(-5.6deg);
        transition: transform 2.8s linear;

        @media(max-width:1919px) {
            width: 501px;
            height: 470px;
            right: -50px;
        }

        @media(max-width:1599px) {
            width: 470px;
            height: 400px;
        }

        @media(max-width:1439px) {
            right: 40px;
            top: 125px;
            width: 410px;
            height: 340px;
        }

        @media(max-width:1023px) {
            width: 329px;
            height: 316px;

            top: 260px;
            left: 50%;
            right: unset;
            transform: translateX(-50%) rotate(240deg);
        }

        @media(max-width:767px) {
            width: 229px;
            height: 216px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }

        &::before {
            content: '';
            width: 465.29px;
            height: 510.78px;

            background: #FF0000;
            filter: blur(400px);

            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: -1;

            @media(max-width:1919px) {
                width: 365.29px;
                height: 360.78px;
                background: #FF0000;
                filter: blur(230px);
            }

            @media(max-width:1023px) {
                display: none;
            }
        }

    }

    .ninja {
        width: 465.29px;
        height: 510.78px;
        position: absolute;

        right: -186px;
        top: 363px;
        z-index: 4;
        transition: transform 1.6s linear;

        @media(max-width:1919px) {
            width: 395px;
            height: 370px;
            right: -56px;
            top: unset;
            bottom: -60px;
        }

        @media(max-width:1439px) {
            width: 325px;
            height: 320px;
            right: 34px;
        }

        @media(max-width:1023px) {
            right: 50%;
            transform: translateX(50%);
            bottom: 142px;
            width: 255px;
            height: 250px;
        }

        @media(max-width:767px) {
            width: 176.75px;
            height: 201.96px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }


    .dots3 {
        width: 297.74px;
        height: 168.39px;

        position: absolute;
        right: -226px;
        top: 203px;
        z-index: 5;

        transform: rotate(-15.01deg);
        transition: transform 2.2s linear;

        @media(max-width:1919px) {
            width: 227px;
            height: 128px;
            right: -66px;
        }

        @media(max-width:1439px) {
            width: 187px;
            height: 78px;
            top: 133px;
            right: 24px;
        }

        @media(max-width:1023px) {
            width: 147.43px;
            height: 95.1px;
            top: 284px;
            right: 30%;
        }

        @media(max-width:767px) {
            width: 97.43px;
            height: 55.1px;
            top: 254px;
            right: 14%;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    .dots4 {
        width: 441.51px;
        height: 253.97px;

        position: absolute;
        right: -103px;
        top: 395px;
        z-index: 5;
        transition: transform 2s linear;

        @media(max-width:1919px) {
            width: 321px;
            height: 173px;
            right: -3px;
            top: 385px;
        }

        @media(max-width:1439px) {
            width: 221px;
            height: 113px;
            right: 187px;
            top: 245px;
        }

        @media(max-width:1023px) {
            top: 356px;
            width: 190.83px;
            height: 122.92px;
            right: unset;
            left: 50%;
            transform: translateX(-50%);
        }

        @media(max-width:767px) {
            top: 316px;
            width: 160.83px;
            height: 92.92px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    .grass {
        height: 475px;
        position: absolute;
        left: -200px;
        right: -200px;
        bottom: -374px;
        z-index: 7;
        transition: transform 2s linear;

        @media(max-width:1919px) {
            height: 305px;
            bottom: -284px;
        }

        @media(max-width:1023px) {
            height: 160px;
            bottom: 0;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top;
        }

        &::before {
            content: '';
            height: 200vh;

            background-image: url("../img/noise.webp");
            background-size: cover;
            background-repeat: no-repeat;
            opacity: .1;
            pointer-events: none;

            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 1;
        }
    }
}

.intro__inner {
    padding-top: 314px;
    padding-bottom: 275px;

    position: relative;

    @media(max-width:1919px) {
        padding-top: 244px;
    }

    @media(max-width:1439px) {
        padding-top: 180px;
    }

    @media(max-width:1023px) {
        padding-top: 119px;
        padding-bottom: 616px;
    }

    @media(max-width:767px) {
        padding-bottom: 394px;
    }
}

.intro__title {
    margin-bottom: 15px;

    font-weight: bold;
    font-size: 120px;
    line-height: 100.4%;
    text-transform: uppercase;
    color: #FFFFFF;

    position: relative;
    z-index: 8;

    @media(max-width:1919px) {
        font-size: 90px;
        line-height: 90%;
    }

    @media(max-width:1599px) {
        font-size: 70px;
        line-height: 70%;
    }

    @media(max-width:1439px) {
        font-size: 55px;
    }

    @media(max-width:1023px) {
        text-align: center;
        margin-bottom: 13px;
    }

    @media(max-width:767px) {
        font-size: 36px;
        line-height: 100.4%;
    }
}

.intro__descr {
    margin-bottom: 30px;
    max-width: 579px;

    font-weight: normal;
    font-size: 24px;
    line-height: 149.4%;
    color: #E8E8E8;

    position: relative;
    z-index: 8;

    @media(max-width:1919px) {
        font-size: 20px;
        line-height: 129%;
        margin-bottom: 15px;
    }

    @media(max-width:1599px) {
        max-width: 519px;
        font-size: 16px;
        line-height: 119%;
    }

    @media(max-width:1439px) {
        max-width: 408px;
        font-size: 14px;
    }

    @media(max-width:1023px) {
        max-width: 280px;
        text-align: center;
        margin: 0 auto;
        line-height: 149.4%;
    }
}

.intro__btns {
    width: 367px;
    display: flex;
    justify-content: space-between;

    position: relative;
    z-index: 8;

    @media(max-width:1023px) {
        flex-direction: column;
        width: 280px;
        height: 110px;
        align-items: center;

        position: absolute;
        bottom: 10px;
        left: 50%;
        transform: translateX(-50%) !important;

        .btn {
            width: 280px;
        }
    }
}