.footer {
    position: relative;
    z-index: 2;

    &::before {
        content: '';
        background-image: url("../img/footer-bg.svg");

        position: absolute;
        background-size: cover;
        background-position: top;
        background-repeat: no-repeat;

        top: -59px;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -1;

        @media(max-width:1023px) {
            display: none;
        }
    }
}

.footer__inner {
    transform: translateY(100%);
    opacity: 0;
    transition: .4s linear;
    transition-delay: .5s;

    &.anim-active {
        transform: translateX(0);
        opacity: 1;
    }
}

.footer__top {
    display: flex;
    align-items: center;
    margin-bottom: 29px;

    @media(max-width:1023px) {
        justify-content: space-between;
    }

    @media(max-width:767px) {
        flex-direction: column;
        margin-bottom: 21px;
    }

    .nav__list {
        margin-right: auto;

        @media(max-width:1023px) {
            margin: 0;
        }

        @media(max-width:767px) {
            margin-bottom: 32px;
        }
    }
}

.footer__logo {
    display: block;
    width: 133px;
    height: 97.37px;
    margin-right: 197px;

    position: relative;

    @media(max-width:1023px) {
        margin-right: 0;
    }

    &::before {
        content: '';
        width: 24.18px;
        height: 20.12px;

        background-image: url("../img/logo-item.png");
        background-size: cover;

        position: absolute;
        top: 26.8px;
        left: 53.6px;


        @media(max-width:767px) {
            width: 19px;
            height: 16px;

            top: 22.8px;
            left: 44.6px;
        }
    }

    @media(max-width:767px) {
        width: 107.91px;
        height: 79px;
        margin-bottom: 27px;

        img {
            width: 107.91px;
            height: 79px;
        }
    }
}

.footer__social-links {
    margin-bottom: 15px;
    justify-content: center;

    @media(max-width:767px) {
        .social-link {
            width: 26px;
            height: 26px;

            img {
                width: 26px;
                height: 26px;
            }

            &+& {
                margin-left: 14px;
            }
        }
    }
}

.footer__copyright {
    margin-bottom: 15px;

    font-weight: normal;
    font-size: 14px;
    line-height: 149.4%;
    text-align: center;
    color: rgba(255, 255, 255, 0.69);
}