.btn {
    width: 173px;
    height: 50px;

    background-color: #9C38DA;
    box-shadow: 0px 0px 43px rgba(156, 56, 218, 0.46);
    border-radius: 50px;
    transition: .2s linear;

    font-weight: bold;
    font-size: 16px;
    line-height: 149.4%;
    color: #FFFFFF;

    &--trans {
        background-color: transparent;
        border: 1px solid #9C38DA;
        box-shadow: none;
        filter: drop-shadow(0px 0px 43px rgba(156, 56, 218, 0.46));

        @media(min-width:1024px) {
            &:hover {
                border-color: darken(#9C38DA, 10%);
                background-color: darken(#9C38DA, 10%);
            }
        }
    }

    @media(min-width:1024px) {
        &:hover {
            background-color: darken(#9C38DA, 10%);
        }
    }
}