.vesting {
    padding-bottom: 81px;
    position: relative;
    z-index: 1;

    @media(max-width:1439px) {
        padding-bottom: 40px;
    }

    @media(max-width:1023px) {
        padding-bottom: 0;
    }

    &::before {
        content: '';
        width: 1334px;
        height: 1565.6px;
        background-image: url("../img/vesting-bg.svg");
        background-size: contain;
        background-repeat: no-repeat;

        position: absolute;
        top: 66px;
        right: 0;
        z-index: 1;

        @media(max-width:1919px) {
            width: 1144px;
            height: 1425.6px;
        }

        @media(max-width:1439px) {
            width: 694px;
            height: 918.6px;
            top: 286px;
        }

        @media(max-width:767px) {
            width: 100%;
            background-size: cover;
            height: 648.6px;
            top: 146px;
        }

        @media(max-width:320px) {
            width: auto;
            background-size: unset;
            left: 0;
            top: 46px;
            height: 1300px;
            background-image: url("../img/mob-bg.svg");
        }
    }


    &::after {
        content: '';
        background-image: url("../img/noise.webp");
        background-size: cover;
        background-repeat: no-repeat;
        opacity: .1;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -1;

        @media(max-width:1023px) {
            display: none;
        }
    }

    .cloud {
        width: 197px;
        height: 107px;

        position: absolute;
        left: -42px;
        top: 231px;

        @media(max-width:1439px) {
            display: none;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }
}

.vesting__inner {
    position: relative;
    z-index: 3;
}

.vesting__title {}

.vesting__descr {
    max-width: 822px;
    margin-bottom: 106px;

    font-weight: bold;
    font-size: 20px;
    line-height: 149.4%;
    color: #FFFFFF;

    @media(max-width:1919px) {
        font-size: 18px;
    }

    @media(max-width:1439px) {
        font-size: 16px;
        max-width: 582px;
        margin-bottom: 50px;
    }

    @media(max-width:1023px) {
        font-size: 14px;
        margin-bottom: 32px;
    }
}

.vesting__road {
    width: 1042.51px;
    height: 1088.5px;
    margin: 0 auto;

    position: relative;
    z-index: 1;

    @media(max-width:1439px) {
        width: 740.51px;
        height: auto;
    }

    @media(max-width:1023px) {
        width: 100%;
    }

    &.anim-active {
        .vesting__svg {
            stroke-dashoffset: 0;
        }

        .vesting__road-title,
        .vesting__road-descr {
            opacity: 1;
            transform: scale(1);
        }

        .rect {
            opacity: 1;
        }
    }
}

.vesting__svg {
    display: block;
    width: 100%;
    height: 100%;
    stroke-dashoffset: 2370.18408203125;
    stroke-dasharray: 2370.18408203125;
    transition: 5s linear;

    @media(max-width:1023px) {
        display: none;
    }
}

.rect {
    opacity: 0;
    transition: .2s linear;
}

.vesting__road-title {
    width: 240px;
    padding-left: 20px;

    font-weight: bold;
    font-size: 18px;
    line-height: 100%;
    text-transform: uppercase;
    color: #FFFFFF;

    transform: scale(.1);
    opacity: 0;
    transition: .4s linear;

    position: absolute;
    z-index: 2;

    &::before {
        content: '';
        width: 9px;
        height: 9px;

        background: #9C38DA;
        border-radius: 50%;

        position: absolute;
        top: 5px;
        left: 0;

        @media(max-width:1439px) {
            @media(min-width:1024px) {
                top: 1px;
                left: 2px;
            }
        }
    }

    &--1 {
        top: 4%;
        left: 5%;

        @media(max-width:1439px) {
            @media(min-width:1024px) {
                width: 180px;
            }
        }
    }

    &--2 {
        top: 14%;
        left: 47%;
    }

    &--3 {
        top: 28%;
        left: 21%;
    }

    &--4 {
        top: 43%;
        left: 20%;
    }

    &--5 {
        top: 48%;
        left: -12%;
    }

    &--6 {
        top: 63%;
        left: 8%;
    }

    &--7 {
        bottom: 14%;
        left: 2%;
    }

    &--8 {
        bottom: 21%;
        left: 26%;
    }

    &--9 {
        bottom: 15%;
        left: 53%;
    }

    &--10 {
        bottom: 35%;
        right: 8%;

        @media(max-width:1439px) {
            @media(min-width:1024px) {
                right: -1%;
            }
        }
    }

    &--11 {
        bottom: 18%;
        right: 0%;

        @media(max-width:1439px) {
            @media(min-width:1024px) {
                right: -9%;
            }
        }

    }

    &--12 {
        bottom: 7%;
        right: -25%;

        @media(max-width:1439px) {
            @media(min-width:1024px) {
                right: -34%;
            }
        }
    }

    @media(max-width:1439px) {
        @media(min-width:1024px) {
            font-size: 12px;
        }
    }

    @media(max-width:1023px) {
        position: relative;
        bottom: unset;
        right: unset;
        left: unset;
        top: unset;

        margin-bottom: 16px;
    }
}

.vesting__road-descr {
    font-weight: normal;
    font-size: 14px;
    line-height: 114.5%;
    color: #FFFFFF;

    transform: scale(.1);
    opacity: 0;
    transition: .2s linear;

    position: absolute;

    &--1 {
        max-width: 226px;

        top: 2%;
        left: 37%;
    }

    &--2 {
        max-width: 187px;
        top: 17%;
        left: 20%;

        @media(max-width:1439px) {
            @media(min-width:1024px) {
                max-width: 127px;
                top: 16%;
                left: 21%;
            }
        }
    }

    &--3 {
        max-width: 186px;
        top: 36%;
        left: 42%;
    }

    &--4 {
        max-width: 116px;

        top: 36%;
        left: 6%;
    }

    &--5 {
        max-width: 191px;

        top: 53%;
        left: 10%;
    }

    &--6 {
        max-width: 162px;

        top: 66%;
        left: -17%;

        @media(max-width:1439px) {
            @media(min-width:1024px) {
                max-width: 132px;
            }
        }
    }

    &--7 {
        max-width: 201px;

        bottom: 26%;
        left: 14%;
        
        @media(max-width:1439px) {
            @media(min-width:1024px) {
                max-width: 101px;
                bottom: 22%;
            }
        }
    }

    &--8 {
        max-width: 203px;

        bottom: 6%;
        left: 24%;
    }

    &--9 {
        max-width: 167px;

        bottom: 27%;
        left: 43%;
    }

    &--10 {
        max-width: 98px;

        bottom: 23%;
        right: 19%;
    }

    &--11 {
        max-width: 134px;

        bottom: 26%;
        right: -9%;
    }

    &--12 {
        font-weight: bold;

        bottom: 7.7%;
        right: 8%;
    }

    @media(max-width:1439px) {
        @media(min-width:1024px) {
            font-size: 12px;
        }
    }

    @media(max-width:1023px) {
        position: relative;
        bottom: unset;
        right: unset;
        left: unset;
        top: unset;

        margin-bottom: 16px;
    }
}