.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;

    padding-top: 65px;
    padding-bottom: 22px;

    transition: .2s linear;

    &.fixed {
        background-color: #1B171E;
        
        @media(min-width:1920px) {
            top: -43px;
        }
    }

    @media(max-width:1919px) {
        padding-top: 22px;
    }
}

.header__inner {
    display: flex;
    align-items: center;

    @media(max-width:1023px) {
        justify-content: space-between;
    }
}

.header__logo {
    display: block;
    width: 133px;
    height: 97.37px;
    margin-right: 149px;

    position: relative;

    &::before {
        content: '';
        width: 24.18px;
        height: 20.12px;

        background-image: url("../img/logo-item.png");
        background-size: cover;

        position: absolute;
        top: 26.8px;
        left: 53.6px;

        @media(max-width:1439px) {
            width: 19px;
            height: 16px;

            top: 22.8px;
            left: 44.6px;
        }
    }

    @media(max-width:1439px) {
        width: 107.91px;
        height: 79px;
        margin-right: 70px;

        img {
            width: 107.91px;
            height: 79px;
        }
    }

    @media(max-width:1023px) {
        order: 2;
        margin-right: 0;
    }
}

.burder {
    display: none;
    width: 43px;
    height: 43px;

    background: #9C38DA;
    box-shadow: 0px 0px 43px rgba(156, 56, 218, 0.46);
    border-radius: 50px;

    @media(max-width:1023px) {
        display: flex;
        align-items: center;
        justify-content: center;
        order: 3;
    }

    &.active {
        .burder__line {
            background-color: transparent;

            &::before {
                bottom: 0;
                transform: rotate(45deg);
            }

            &::after {
                top: 0;
                transform: rotate(-45deg);
            }
        }
    }
}

.burder__line {
    width: 20px;
    height: 2px;

    background: #FFFFFF;
    transition: .3s linear;

    position: relative;

    &::before {
        content: '';
        background: #FFFFFF;
        height: 2px;

        position: absolute;
        left: 0;
        right: 0;
        bottom: calc(100% + 4px);
        transition: .3s linear;
    }

    &::after {
        content: '';
        background: #FFFFFF;
        height: 2px;

        position: absolute;
        left: 0;
        right: 0;
        top: calc(100% + 4px);
        transition: .3s linear;
    }
}

.header__btn {
    @media(max-width:1023px) {
        display: none;
    }
}

.header__contacts {
    display: none;

    @media(max-width:1023px) {
        display: block;
        margin-bottom: 34px;
    }
}

.header__social-links {
    display: none !important;

    @media(max-width:1023px) {
        display: flex !important;

        .social-link {
            width: 26px;
            height: 26px;

            img {
                display: block;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            &+& {
                margin-left: 14px;
            }
        }
    }
}