.team {
    padding-top: 51px;
    padding-bottom: 184px;

    @media(max-width:1023px) {
        padding-bottom: 93px;
    }

    position: relative;
    z-index: 1;

    &::before {
        content: '';
        background: linear-gradient(180deg, rgba(36, 30, 40, 0) 0%, #241E28 24.19%, #241E28 100%);

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -1;
    }

    .cloud {
        width: 212px;
        height: 115px;

        position: absolute;
        right: 105px;
        top: 83px;
        z-index: 2;

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }

        @media(max-width:1023px) {
            display: none;
        }
    }

    .cloud2 {
        width: 163px;
        height: 1189.6px;

        position: absolute;
        left: 0;
        top: -470px;
        z-index: 2;

        @media(max-width:1439px) {
            display: none;
        }
    }
}

.team__inner {
    position: relative;
    z-index: 3;
}

.team__title {
    text-align: center;

    @media(max-width:1023px) {
        margin-bottom: 10px;
    }
}

.team__text {
    max-width: 953px;
    margin: 0 auto 14px;

    font-weight: bold;
    font-size: 24px;
    line-height: 126.4%;
    text-align: center;
    color: #FFFFFF;

    @media(max-width:1919px) {
        font-size: 20px;
    }

    @media(max-width:1439px) {
        font-size: 16px;
    }

    @media(max-width:1023px) {
        font-size: 14px;
        margin-bottom: 20px;
    }
}

.team__descr {
    max-width: 945px;
    margin: 0 auto 59px;

    font-weight: normal;
    font-size: 16px;
    line-height: 149.4%;
    text-align: center;
    color: rgba(255, 255, 255, 0.69);

    @media(max-width:1919px) {
        font-size: 14px;
        margin-bottom: 35px;
    }

    @media(max-width:1439px) {
        margin-bottom: 20px;
    }
}

.team__slider {
    @media(max-width:767px) {
        margin: 0 -10px;
    }
}

.team__slider-btn {
    display: none;

    @media(max-width:1023px) {
        display: block;
        width: 50px;
        height: 50px;

        background-image: url("../img/slider-btn.svg");

        position: absolute;
        top: 63px;
        z-index: 2;
    }
}

.team__slider-btn--prev {
    left: 0;
}

.team__slider-btn--next {
    right: 0;
    transform: rotate(180deg);
}

.team__list {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.team__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: calc(25% - 20px);
    margin: 0 10px;

    @media(min-width:1024px) {
        flex-shrink: unset;
    }

    @media(max-width:1023px) {
     width: auto;
     margin: 0;
    }
}

.team__photo {
    display: block;
    width: 100%;
    margin-bottom: 21px;
    position: relative;

    &:after {
        content: '';
        display: block;
        padding-top: 100%;
    }

    @media(max-width:1023px) {
        width: 172px;
    }
}

.team__photo-inner {
    border-radius: 100%;
    overflow: hidden;

    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
}

.team__name {
    font-weight: normal;
    font-size: 24px;
    line-height: 134.9%;
    text-align: center;
    color: #FFFFFF;
}

.team__position {
    margin-bottom: 16px;

    font-weight: 600;
    font-size: 18px;
    line-height: 134.9%;
    color: #FFFFFF;
    text-transform: uppercase;
    text-align: center;
}

.social-links {
    display: flex;
    align-items: center;
}

.social-link {
    transition: .2s linear;

    @media(min-width:1024px) {
        &:hover {
            transform: scale(1.1);
        }
    }

    &+& {
        margin-left: 15px;
    }
}