.tokenomics {
    padding-top: 80px;
    padding-bottom: 322px;

    position: relative;
    z-index: 2;

    @media(max-width:1439px) {
        padding-top: 40px;
    }

    @media(max-width:1023px) {
        padding-bottom: 200px;
    }

    @media(max-width:767px) {
        padding-bottom: 152px;
    }

    &::after {
        content: '';
        background-image: url("../img/waves-bg.webp");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        opacity: 0.8;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -1;
    }

    &::before {
        content: '';
        background-image: url("../img/noise.webp");
        background-size: cover;
        background-repeat: no-repeat;
        opacity: .1;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
    }
}

.tokenomics__inner {
    position: relative;
    z-index: 3;
}

.tokenomics__title {
    margin-bottom: 12px;
    text-align: center;
}

.tokenomics__descr {
    max-width: 748px;
    margin: 0 auto 47px;

    font-weight: normal;
    font-size: 16px;
    line-height: 149.4%;
    text-align: center;
    color: rgba(255, 255, 255, 0.69);

    @media(max-width:1919px) {
        font-size: 14px;
    }
}

.tokenomics__supply {
    width: 739px;
    height: 632px;
    margin: 0 auto;
    padding-top: 159px;

    background-image: url("../img/moon3.svg");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;

    position: relative;
    z-index: 1;

    @media(max-width:1023px) {
        width: 679px;
        height: 602px;
        padding-top: 109px;
    }

    @media(max-width:767px) {
        width: 275px;
        height: 234px;
        padding-top: 53px;
    }

    &::before {
        content: '';

        width: 896px;
        height: 728.5px;

        background: radial-gradient(50% 61.49% at 50% 61.49%, #FF0000 0%, rgba(255, 0, 0, 0) 100%);
        opacity: 0.5;

        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: -1;

        @media(max-width:1023px) {
            display: none;
        }
    }
}

.tokenomics__supply-title {
    font-weight: 600;
    font-size: 40px;
    line-height: 149.4%;
    text-align: center;
    color: #FFFFFF;

    @media(max-width:1919px) {
        font-size: 36px;
    }

    @media(max-width:1439px) {
        font-size: 28px;
    }

    @media(max-width:1023px) {
        font-size: 24px;
    }
}

.tokenomics__supply-value {
    font-weight: bold;
    font-size: 64px;
    line-height: 90.4%;
    color: #FFFFFF;
    text-align: center;

    @media(max-width:1919px) {
        font-size: 52px;
    }

    @media(max-width:1439px) {
        font-size: 46px;
    }

    @media(max-width:1023px) {
        font-size: 36px;
    }
}

.tokenomics__supply-name {
    font-weight: normal;
    font-size: 36px;
    line-height: 90.4%;
    text-align: center;
    color: #FFFFFF;
}

.tokenomics__info {
    width: 993px;
    height: 356px;

    background-image: url("../img/tokenomics-info.svg");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;

    position: absolute;
    top: 351px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;

    @media(max-width:1023px) {
        width: 883px;
        height: 316px;
    }

    @media(max-width:767px) {
        width: 397px;
        height: 141.1px;
        top: 191px;
    }
}

.tokenomics__info-item {
    width: 167px;
    position: absolute;

    @media(max-width:1023px) {
        width: 150px;
    }

    @media(max-width:767px) {
        width: 63.4px;
    }

    &:nth-child(1) {
        bottom: 26%;
        left: 5.5%;
    }

    &:nth-child(2) {
        bottom: 44%;
        left: 17%;
        transition-delay: .2s;

    }

    &:nth-child(3) {
        top: 25%;
        left: 28.8%;
        transition-delay: .3s;
    }

    &:nth-child(4) {
        left: 28.8%;
        bottom: 26%;
        transition-delay: .2s;
    }

    &:nth-child(5) {
        top: 7%;
        left: 40.5%;
        transition-delay: .3s;
    }

    &:nth-child(6) {
        bottom: 44%;
        left: 40.5%;

        transition-delay: .4s;
    }

    &:nth-child(7) {
        bottom: 6%;
        left: 40.5%;
        transition-delay: .2s;
    }

    &:nth-child(8) {
        top: 25%;
        right: 30.3%;

        transition-delay: .4s;
    }

    &:nth-child(9) {
        bottom: 26%;
        right: 30.3%;
        transition-delay: .3s;
    }

    &:nth-child(10) {
        bottom: 44%;
        right: 19%;

        transition-delay: .4s;
    }

    &:nth-child(11) {
        bottom: 26%;
        right: 7.6%;
        transition-delay: .3s;
    }
}

.tokenomics__info-num {
    font-weight: bold;
    font-size: 36px;
    line-height: 91.4%;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;

    @media(max-width:1919px) {
        font-size: 28px;
    }

    @media(max-width:1439px) {
        font-size: 22px;
    }

    @media(max-width:767px) {
        font-size: 10px;
        line-height: 91.4%;
    }
}

.tokenomics__info-descr {
    font-weight: normal;
    font-size: 16px;
    line-height: 100%;
    text-align: center;
    color: #FFFFFF;

    @media(max-width:1919px) {
        font-size: 14px;
    }

    @media(max-width:1023px) {
        font-size: 12px;
    }

    @media(max-width:767px) {
        font-size: 10px;
    }
}

.dots-left,
.dots-right {
    width: 121.03px;
    height: 439.39px;

    position: absolute;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center;
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.dots-left {
    left: -35px;
    top: 295px;
}

.dots-right {
    right: -35px;
    bottom: -25px;
}