.nav {
    display: flex;
    align-items: center;


    @media(max-width:1439px) {
        margin-right: auto;
    }

    @media(max-width:1023px) {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        z-index: -1;

        opacity: 0;
        visibility: hidden;
        transition: .3s linear;

        height: 100vh;
        flex-direction: column;
        padding-top: 132px;
        background: rgba(26, 22, 29, 0.97);

        &.active {
            visibility: visible;
            opacity: 1;
        }
    }
}

.nav__list {
    display: flex;
    align-items: center;
    margin-right: 125px;

    @media(max-width:1023px) {
        width: 100%;
        justify-content: center;
        margin-right: 0;
        margin-bottom: 32px;
    }

    @media(max-width:767px) {
        width: 274.68px;
        flex-wrap: wrap;
        justify-content: space-between;
    }
}

.nav__item {
    &+& {
        margin-left: 60px;

        @media(max-width:1439px) {
            margin-left: 20px;
        }

        @media(max-width:767px) {
            margin-left: 0;
        }
    }

    @media(max-width:767px) {
        width: 99.28px;

        &:nth-child(1) {
            order: 1;
        }
        &:nth-child(2) {
            order: 3;
            margin-top: 9px;
        }
        &:nth-child(3) {
            order: 2;
        }
        &:nth-child(4) {
            order: 4;
            margin-top: 9px;
        }
    }
}

.nav__link {
    padding-left: 14px;

    font-weight: normal;
    font-size: 16px;
    line-height: 149.4%;
    color: #fff;

    transition: .2s linear;

    position: relative;

    &::before {
        content: '';
        width: 6px;
        height: 11px;
        background-image: url("../img/arrow.svg");

        filter: drop-shadow(0px 0px 3px rgba(156, 56, 218, 0.75));

        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
    }

    @media(min-width:1024px) {
        &:hover {
            color: #9C38DA;
            filter: drop-shadow(0px 0px 3px rgba(156, 56, 218, 0.75));
        }
    }
}

.langs {
    margin-right: 34px;
    padding-right: 24px;

    // cursor: pointer;

    position: relative;

    @media(max-width:1023px) {
        order: 1;
        margin-right: 0;
    }

