* {
    font-family: "Proxima Nova", sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 149.4%;
    color: #fff;
}

body {
    background: #1B171E;

    &.scroll-hide {
        overflow: hidden;
    }
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 30px;

    @media(max-width:1023px) {
     padding: 0 20px;
    }
}

.page {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
}

.title {
    font-weight: bold;
    font-size: 72px;
    line-height: 149.4%;
    text-transform: uppercase;
    color: #FFFFFF;

    @media(max-width:1919px) {
        font-size: 62px;
    }

    @media(max-width:1439px) {
        font-size: 48px;
    }

    @media(max-width:1023px) {
        font-size: 36px;
        line-height: 100.4%;
    }
}

.anim {

    &--one {
        opacity: 0;
        transform-origin: center top;
        transform-style: preserve-3d;
        transform: translateY(100%) rotateX(-80deg);
        transition: opacity 0s cubic-bezier(0.215, 0.61, 0.355, 1),
            transform 0s cubic-bezier(0.215, 0.61, 0.355, 1);
        transition-duration: 0.25s;
    }

    &--two {
        opacity: 0;
        transform-origin: center top;
        transform-style: preserve-3d;
        transform: translateY(100%) rotateX(-80deg);
        transition: opacity 0s cubic-bezier(0.215, 0.61, 0.355, 1),
            transform 0s cubic-bezier(0.215, 0.61, 0.355, 1);
        transition-delay: .2s;
        transition-duration: 0.25s;
    }

    &--three {
        opacity: 0;
        transform-origin: center top;
        transform-style: preserve-3d;
        transform: translateY(100%) rotateX(-80deg);
        transition: opacity 0s cubic-bezier(0.215, 0.61, 0.355, 1),
            transform 0s cubic-bezier(0.215, 0.61, 0.355, 1);
        transition-delay: .4s;
        transition-duration: 0.25s;

    }

    &--title {
        transform: translateY(100%);
        opacity: 0;
        transition-duration: 0.25s;
    }

    &--text {
        transform: translateY(100%);
        opacity: 0;
        transition-delay: .2s;
        transition-duration: 0.25s;

    }

    &--text-left {
        transform: translateX(100%);
        opacity: 0;
        transition-delay: .2s;
        transition-duration: 0.25s;

        &:nth-child(2) {
            transition-delay: .3s;
        }

        &:nth-child(3) {
            transition-delay: .4s;
        }

        &:nth-child(4) {
            transition-delay: .5s;
        }

        &:nth-child(5) {
            transition-delay: .6s;
        }
    }

    &--text-right {
        transform: translateX(-100%);
        opacity: 0;
        transition-delay: .2s;
        transition-duration: 0.25s;
    }

    &--scale {
        transform: scale(.1);
        opacity: 0;
        transition-duration: 0.4s;


        &:nth-child(2) {
            transition-delay: .2s;
        }

        &:nth-child(3) {
            transition-delay: .3s;
        }
        &:nth-child(4) {
            transition-delay: .4s;
        }

        &:nth-child(5) {
            transition-delay: .5s;
        }
    }

    &.anim-active {
        transform: none;
        opacity: 1;
    }
}

