.problems {
    padding-bottom: 184px;

    position: relative;

    @media(max-width:1023px) {
        padding-bottom: 20px;
    }

    &::before {
        content: '';
        background-image: url("../img/waves-bg.webp");
        opacity: 0.7;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;

        @media(max-width:1023px) {
            display: none;
        }
    }

    &::after {
        content: '';
        background-image: url("../img/noise.webp");
        background-size: cover;
        background-repeat: no-repeat;
        opacity: .1;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;

        @media(max-width:1023px) {
            display: none;
        }
    }

    .cloud {
        width: 231px;
        height: 126px;

        position: absolute;
        top: 193px;
        left: 119px;
        z-index: 3;

        @media(max-width:1919px) {
            width: 161px;
            height: 76px;
            top: 153px;
            left: 99px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }

    .stars {
        width: 597.46px;
        height: 254.99px;

        position: absolute;
        top: 175px;
        left: -194px;
        transform: rotate(-54deg);
        z-index: 3;

        @media(max-width:1919px) {
            width: 297.46px;
            height: 164.99px;
            top: 95px;
            left: -84px;
        }

        @media(max-width:1439px) {
            display: none;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }

    .stars2 {
        width: 612.62px;
        height: 306.7px;

        position: absolute;
        top: -70px;
        left: 119px;
        z-index: 3;

        @media(max-width:1919px) {
            width: 312.62px;
            height: 156.7px;
        }

        @media(max-width:1439px) {
            display: none;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }

    .moon {
        width: 285px;
        height: 245px;

        position: absolute;
        right: 234px;
        top: -110px;
        z-index: 3;

        @media(max-width:1919px) {
            width: 225px;
            height: 205px;
            right: 164px;
            top: -71px;
        }

        @media(max-width:1439px) {
            width: 185px;
            height: 165px;
            right: 94px;
            top: -201px;
        }

        @media(max-width:1023px) {
            width: 58.94px;
            height: 50.93px;
            right: 18px;
            top: -31px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }

    .cloud2 {
        width: 292px;
        height: 159px;

        position: absolute;
        right: 24px;
        top: -16px;
        z-index: 3;

        @media(max-width:1919px) {
            width: 242px;
            height: 109px;
            right: 24px;
            top: 19px;
        }

        @media(max-width:1439px) {
            width: 172px;
            height: 79px;
            right: 24px;
            top: -101px;
        }

        @media(max-width:1023px) {
            display: none;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }

    .stars3 {
        width: 370.51px;
        height: 209.55px;

        position: absolute;
        top: 0;
        right: 261px;
        z-index: 3;
        transform: rotate(-15.94deg);

        @media(max-width:1919px) {
            width: 260.51px;
            height: 169.55px;
            top: 0;
            right: 211px;
        }

        @media(max-width:1439px) {
            width: 200.51px;
            height: 79.55px;
            top: -190px;
            right: 131px;
        }

        @media(max-width:1023px) {
            width: 76.95px;
            height: 43.52px;
            right: 20px;
            top: -4px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }

    .katana {
        width: 842.38px;
        height: 749.53px;

        position: absolute;
        top: 0;
        right: 0;
        transition: transform .3s linear;
        transform: translate(100%, 100%) rotate(10deg);
        z-index: 3;

        @media(max-width:1919px) {
            width: 592.38px;
            height: 638.53px;
        }

        @media(max-width:1439px) {
            width: 442.38px;
            height: 408.53px;
            top: -150px;
        }

        @media(max-width:1023px) {
            width: 174.94px;
            height: 155.65px;
            right: -30px;
            top: -5px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }

        &.anim-active {
            transform: translate(0, 0) rotate(10deg);
        }
    }

    .stars4 {

        width: 278px;
        height: 306.7px;

        position: absolute;
        bottom: 117px;
        right: 0;
        z-index: 3;

        @media(max-width:1919px) {
            width: 188px;
            height: 176.7px;
        }

        @media(max-width:1439px) {
            display: none;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }

    .waves {
        height: 154px;

        position: absolute;
        left: -16px;
        right: -32px;
        bottom: -52px;
        z-index: 3;

        @media(max-width:1919px) {
            height: 100px;
        }

        @media(max-width:1023px) {
            display: none;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top;
        }
    }
}

.problems__title {
    margin-bottom: 23px;

    @media(max-width:1919px) {
        margin-bottom: 15px;
    }

    @media(max-width:767px) {
        max-width: 212px;
    }
}

.problems__inner {
    position: relative;
    z-index: 4;
}

.problems__descr {

    font-weight: normal;
    font-size: 18px;
    line-height: 149.4%;
    color: rgba(255, 255, 255, 0.69);

    @media(max-width:1919px) {
        font-size: 19px;
    }

    @media(max-width:1023px) {
        max-width: 580px !important;
        font-size: 14px;
        line-height: 149.4%;
    }

    @media(max-width:767px) {
        max-width: 263px !important;
    }

    &--one {
        max-width: 779px;
        margin-bottom: 17px;
    }

    &--two {
        max-width: 861px;
        margin-bottom: 31px;
    }

    &--three {
        max-width: 1136px;
        transition-delay: .7s;
    }
}

.problems__list {
    margin-bottom: 36px;
}

.problems__item {
    display: flex;
    align-items: center;

    &+& {
        margin-top: 25px;

        @media(max-width:1023px) {
            margin-top: 20px;
        }
    }
}

.problems__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 39px;
    height: 39px;
    margin-right: 20px;

    background: #9C38DA;
    box-shadow: 0px 0px 43px rgba(156, 56, 218, 0.46);
    border-radius: 50px;

    @media(max-width:767px) {
        min-width: 39px;
    }
}

.problems__text {
    font-weight: bold;
    font-size: 20px;
    line-height: 126.4%;
    color: #FFFFFF;

    @media(max-width:1919px) {
        font-size: 18px;
    }

    @media(max-width:1599px) {
        font-size: 16px;
    }

    @media(max-width:1023px) {
        font-size: 14px;
    }
}

.problems__bg {
    width: 974px;
    height: 578.35px;

    position: absolute;
    bottom: -107px;
    right: 0;
    z-index: 3;

    @media(max-width:1919px) {
        width: 804px;
        height: 518.35px;
    }

    @media(max-width:1023px) {
        display: none;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center;
    }
}