.capabilities {
    padding-bottom: 52px;

    @media(max-width:767px) {
        padding-bottom: 0;
    }

    position: relative;
    z-index: 4;

    &::after {
        content: '';
        background-image: url("../img/noise.webp");
        background-size: cover;
        background-repeat: no-repeat;
        opacity: .1;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
    }

    .cloud {
        width: 231px;
        height: 126px;

        position: absolute;
        bottom: 315px;
        left: 134px;
        z-index: 5;

        @media(max-width:1919px) {
            width: 161px;
            height: 88px;
        }

        @media(max-width:767px) {
            bottom: unset;
            top: 240px;
            left: -46px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }

    .cloud2 {
        width: 215px;
        height: 115px;

        position: absolute;
        top: -40px;
        right: 181px;
        z-index: 5;

        @media(max-width:1919px) {
            width: 148px;
            height: 80px;
        }

        @media(max-width:767px) {
            top: unset;
            bottom: 330px;
            right: -38px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }

    .stars {
        width: 533px;
        height: 266.84px;

        position: absolute;
        left: 99px;
        top: 40px;
        z-index: 5;

        @media(max-width:1919px) {
            width: 433px;
            height: 176.84px;
        }

        @media(max-width:767px) {
            width: 533px;
            height: 266.84px;
            left: -40px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }

    .stars2 {
        width: 546.34px;
        height: 182.63px;

        position: absolute;
        right: 188px;
        top: 132px;
        z-index: 5;

        @media(max-width:1919px) {
            width: 406.34px;
            height: 112.63px;
        }

        @media(max-width:767px) {
            width: 533px;
            height: 266.84px;
            top: unset;
            bottom: 330px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }
}

.capabilities__inner {
    position: relative;
    z-index: 6;
}

.capabilities__title {
    margin-bottom: 22px;
    text-align: center;

    @media(max-width:767px) {
        margin-bottom: 32px;
    }
}

.capabilities__list {
    display: flex;
    justify-content: space-around;
    margin-bottom: 36px;

    @media(max-width:767px) {
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        margin-bottom: 28px;
    }
}

.capabilities__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;

    @media(max-width:767px) {
        justify-content: center;
    }
}

.capabilities__icon {
    max-width: 149px;
    height: auto;
    margin-bottom: 18px;

    img {
        display: block;
        width: 100%;
        height: auto;
        object-fit: contain;
    }
}

.capabilities__text {
    max-width: 200px;

    font-weight: bold;
    font-size: 24px;
    line-height: 126.4%;
    text-align: center;
    color: #FFFFFF;
}

.capabilities__descr {
    max-width: 1076px;
    margin: 0 auto;

    font-weight: normal;
    font-size: 16px;
    line-height: 149.4%;
    text-align: center;
    color: rgba(255, 255, 255, 0.69);

    @media(max-width:1023px) {
        font-size: 14px;
    }
}