.faq {
    padding-bottom: 136px;
    padding-top: 178px;

    position: relative;
    z-index: 2;

    @media(max-width:1439px) {
        padding-top: 100px;
    }

    @media(max-width:767px) {
        padding-top: 30px;
        padding-bottom: 54px;
    }

    &::before {
        content: '';
        height: 154px;

        background-image: url("../img/faq-top.svg");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: top;

        position: absolute;
        top: -80px;
        left: 0;
        right: 0;

        @media(max-width:1439px) {
            top: -50px;
            height: 81px;
        }

        @media(max-width:1023px) {
            display: none;
        }
    }

    &::after {
        content: '';
        background-image: url("../img/waves-bg.webp");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        opacity: 0.4;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -1;

        @media(max-width:1023px) {
            display: none;
        }
    }

    .moon {
        width: 258px;
        height: 221px;

        position: absolute;
        left: 13px;
        top: 212px;

        @media(max-width:1919px) {
            width: 168px;
            height: 131px;
        }

        @media(max-width:1439px) {
            display: none;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }

    .cloud {
        width: 226px;
        height: 123px;

        position: absolute;
        left: 136px;
        top: 351px;

        @media(max-width:1919px) {
            width: 146px;
            height: 63px;
            left: 76px;
            top: 291px;
        }

        @media(max-width:1439px) {
            display: none;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }

    .rock {
        width: 1425px;
        height: 839.44px;

        position: absolute;
        bottom: 0;
        right: 0;

        @media(max-width:1919px) {
            width: 995px;
            height: 589.44px;
        }

        @media(max-width:1023px) {
            display: none;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }

    .ninja {
        width: 355px;
        height: 599px;

        transform: translateX(200%);
        transition: .6s linear;

        position: absolute;
        bottom: 42px;
        right: 495px;

        @media(max-width:1919px) {
            width: 235px;
            height: 359px;
            bottom: 42px;
            right: 205px;
        }

        @media(max-width:1023px) {
            display: none;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }

        &.anim-active {
            transform: none;
        }
    }
}

.faq__inner {
    position: relative;
    z-index: 3;
}

.faq__title {
    text-align: center;
    margin-bottom: 94px;

    @media(max-width:1439px) {
        margin-bottom: 55px;
    }

    @media(max-width:767px) {
        margin-bottom: 16px;
    }
}

.faq__item {
    width: 532px;

    background: #211C23;
    border-radius: 26px;

    &+& {
        margin-top: 20px;
    }

    @media(max-width:767px) {
        max-width: 532px;
        width: 100%;
    }
}

.faq__top {
    width: 100%;
    padding: 14px 31px 13px;

    background: #341E3E;
    border-radius: 39px;
    cursor: pointer;
    transition: .3s linear;

    font-weight: bold;
    font-size: 18px;
    line-height: 126.4%;
    color: #FFFFFF;

    position: relative;

    @media(min-width:1024px) {
        &:hover:not(.active) {
            background: lighten(#341E3E, 10%);
        }
    }

    &::before {
        content: '';
        width: 19px;
        height: 10px;

        background-image: url("../img/arrow-down.svg");
        background-size: contain;
        background-repeat: no-repeat;
        transition: .3s linear;

        position: absolute;
        top: 20px;
        right: 25px;
    }

    &.active {
        background: #9C38DA;

        &::before { 
            transform: rotate(180deg);
        }
    }
}

.faq__content {
    display: none;
    padding: 17px 17px 11px 25px;

    font-weight: normal;
    font-size: 14px;
    line-height: 149.4%;
    color: rgba(255, 255, 255, 0.69);
}