.collections {
    padding-bottom: 285px;
    position: relative;

    @media(max-width:1023px) {
     padding-bottom: 104px;
    }

    @media(max-width:767px) {
        padding-bottom: 28px;
    }

    &::after {
        content: '';
        height: 765.11px;

        background-image: url("../img/collections-bg.svg");
        background-size: cover;
        background-repeat: no-repeat;

        position: absolute;
        top: 50px;
        left: 0;
        right: 0;

        @media(max-width:1023px) {
            display: none;
        }
    }

    &::before {
        content: '';
        background-image: url("../img/noise.webp");
        background-size: cover;
        background-repeat: no-repeat;
        opacity: .1;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;

        @media(max-width:1023px) {
            display: none;
        }
    }

    .blot {
        width: 821px;
        height: 505px;

        position: absolute;
        right: 183px;
        top: 376px;

        @media(max-width:1439px) {
            display: none;
        }

        &::before {
            content: '';
            height: 333px;

            background: rgba(133, 54, 170, 0.55);
            filter: blur(220px);

            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
        }
    }

    .stars {
        width: 612.62px;
        height: 306.7px;

        position: absolute;
        top: 249px;
        left: 22px;

        @media(max-width:1919px) {
            width: 372.62px;
            height: 196.7px;
        }

        @media(max-width:1023px) {
            display: none;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center;
        }
    }

    .grass {
        height: 475px;

        position: absolute;
        left: 0;
        right: 0;
        bottom: 13px;
        z-index: 2;

        @media(max-width:1919px) {
            height: 295px;
        }

        @media(max-width:1439px) {
            bottom: 163px;
        }

        @media(max-width:1023px) {
            display: none;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top;
        }
    }
}

.collections__inner {
    padding-top: 208px;
    position: relative;
    z-index: 3;

    @media(max-width:1439px) {
        padding-top: 88px;
    }
}

.collections__title {
    text-align: center;
    margin-bottom: 154px;

    @media(max-width:1919px) {
        margin-bottom: 65px;
    }
}

.collections__descr {
    @media(max-width:1439px) {
        padding-top: 300px;
    }

    @media(max-width:1023px) {
        padding-top: 267px;
    }
}

.collections__descr-title {
    max-width: 566px;
    padding-bottom: 16px;
    margin-bottom: 7px;

    font-weight: bold;
    font-size: 24px;
    line-height: 126.4%;
    color: #FFFFFF;

    position: relative;

    @media(max-width:1919px) {
     font-size: 22px;
    }

    @media(max-width:1439px) {
        font-size: 20px;
    }

    @media(max-width:1023px) {
        font-size: 18px;
        line-height: 126.4%;
    }

    &::before {
        content: '';
        width: 89px;
        height: 2px;

        background-color: #9C38DA;

        position: absolute;
        left: 0;
        bottom: 0;
    }
}

.collections__item {
    font-weight: normal;
    font-size: 18px;
    line-height: 149.4%;
    color: rgba(255, 255, 255, 0.69);

    @media(max-width:1919px) {
     font-size: 16px;
    }

    @media(max-width:1023px) {
        font-size: 14px;
    }

    &--one {
        max-width: 619px;
        margin-bottom: 28px;
    }

    &--two {
        max-width: 619px;
        margin-bottom: 17px;
    }

    &--three {
        max-width: 649px;
    }
}

.collections__card {
    width: 428.2px;
    height: 499.16px;

    position: absolute;
    transition: transform .35s linear;
    transition-delay: .2s;

    
    @media(max-width:1919px) {
        width: 360.2px;
        height: 400.16px;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center;
    }

    &--one {
        top: 360px;
        right: 60px;

        transform: translate(130px, 50px) rotate(-14deg);

        @media(max-width:1599px) {
            width: 340px;
            height: 380px;
            top: 340px;
            right: 110px;
        }

        @media(max-width:1439px) {
            width: 260px;
            height: 290px;
            top: 190px;
            right: unset;
            left: 110px;
        }

        @media(max-width:1023px) {
            width: 170.48px;
            height: 198.73px;
            left: -20px;
        }

        &.anim-active {
            transform: translate(0, 0) rotate(-3.46deg);
        }
    }

    &--two {
        width: 421.2px;
        height: 559.16px;

        top: 480px;
        right: -90px;
        transform: translate(-23px, -99px) rotate(-14deg);

        @media(max-width:1919px) {
            width: 351.2px;
            height: 499.16px;
            top: 380px;
            right: -80px;
        }

        @media(max-width:1599px) {
            width: 331.2px;
            height: 429.16px;
            top: 380px;
            right: 0px;
        }

        @media(max-width:1439px) {
            width: 281.2px;
            height: 339.16px;
            top: 180px;
            right: unset;
            left: 220px;
        }

        @media(max-width:1023px) {
            width: 181.2px;
            height: 229.16px;
            top: 230px;
            right: unset;
            left: 40px;
        }

        &.anim-active {
            transform: translate(0, 0) rotate(4.17deg);
        }
    }

    &--three {
        width: 421.2px;
        height: 559.16px;
        right: -272px;
        top: 448px;
        transform: translate(-205px, -67px) rotate(-14deg);

        @media(max-width:1919px) {
            width: 351.2px;
            height: 499.16px;
            top: 448px;
            right: -192px;
        }

        @media(max-width:1599px) {
            width: 341.2px;
            height: 439.16px;
            top: 448px;
            right: -102px;
        }

        
        @media(max-width:1439px) {
            width: 281.2px;
            height: 339.16px;
            top: 180px;
            right: unset;
            left: 320px;
        }

        @media(max-width:1023px) {
            width: 181.2px;
            height: 229.16px;
            top: 220px;
            right: unset;
            left: 110px;
        }

        &.anim-active {
            transform: translate(0, 0) rotate(14.78deg);
        }
    }


}